//const debug = OOP.debug(spheres, ['id', 'x', 'y', 'fillColor']);
const dbg = function (pjs) {
    const x = 110;
    const y = 110;
    const padding = 5;
    const color = '#ffffff';
    const fillColor = '#373737';
    const strokeColor = '#3f3f3f';
    const lineColor = '#cc88bd';
    const strokeWidth = 1;
    const textSize = 15;
    const radius = 5;
    const mouse = pjs.mouseControl.initMouseControl();
    const game = pjs.game;
    const random = pjs.math.random;

    let items = [];

    return {
        remove: function (element) {
            const index = items.findIndex(item => item.element === element);
            if (index > -1){
                items.splice(index, 1);
            }
        },
        add: function (element, options) {
            const gs = game.getWH();
            let width  = null;

            OOP.forArr(options, (option) => {
                let result = element[option];

                const textWidth = OOP.getTextWidth({size: textSize, text: `${option}: ${result}`});

                if (width === null || width < textWidth) {
                    width = textWidth;
                }
            });

            const height = (padding * 2) + (options.length * textSize);

            items.push({
                x: random(0, gs.w - 300 - width + padding * 2),
                y: random(0, gs.h - height),
                w: width,
                h: height,
                isMove: false,
                element,
                options
            });
        },
        draw: function () {

            OOP.forArr(items, (item) => {
                const element = item.element;

                const mp = mouse.getPosition();

                if (mouse.isObject(item) && mouse.isPress('LEFT')) {
                    item.isMove = point(mp.x - item.x, mp.y - item.y);
                }

                if (item.isMove) {

                    item.x = mp.x - item.isMove.x;
                    item.y = mp.y - item.isMove.y;

                    if (mouse.isUp('LEFT')) {
                        item.isMove = false;
                    }
                }


                let lines = [];

                item.w = null;
                OOP.forArr(item.options, (option, optionID) => {
                    let result = element[option];

                    if (typeof result === 'number') {
                        result = parseInt(result);
                    }

                    const line = {
                        x: item.x + padding,
                        y: item.y + padding + (textSize * optionID),
                        color: color,
                        size: textSize,
                        text: `${option}: ${result}`
                    };

                    const textWidth = OOP.getTextWidth(line);

                    if (item.w === null || item.w < textWidth) {
                        item.w = textWidth;
                    }

                    lines.push(line);
                });

                item.w += padding * 2;

                const rectCenter = point(item.x + (item.w / 2), item.y + (item.h / 2));
                const elementCenter = element.getPositionC();

                brush.drawLineAS({
                    x1: rectCenter.x,
                    y1: rectCenter.y,
                    x2: elementCenter.x,
                    y2: elementCenter.y,
                    strokeWidth: 1,
                    strokeColor: lineColor
                });


                brush.drawRoundRect({
                    x: item.x,
                    y: item.y,
                    w: item.w,
                    h: item.h,
                    fillColor,
                    strokeColor,
                    strokeWidth,
                    radius
                });

                OOP.forArr(lines, e => brush.drawText(e));
            });


        }
    }
};

module.exports = dbg;


