import {$} from "./shtml";

require('./common.scss');


PointJS = function (e) {
    return function () {
        e.apply(this, arguments);

        const pjs = this;
        pjs.mouseControl.isObject = function (object) {
            const mp = pjs.mouseControl.getPosition();
            return mp.x > object.x && mp.x < object.x + object.w && mp.y > object.y && mp.y < object.y + object.h
        };
        const dbg = require('./debugger')(pjs);

        const createElement = function (tagName) {
            const element = document.createElement(tagName);
            element.onmousemove = element.onmousedown = element.onmouseup = element.onselectstart = element.ondragstart = element.onmousewheel = function (e) {
                return e.stopPropagation();
            };
            return element;
        };

        const statsHeight = 60;
        const canvas = $(createElement('canvas')).addClass('pointDebugger');
        const context = canvas.selector[0].getContext('2d');
        const menu = $(createElement('div')).addClass('pointDebugger');
        const wrapper = $(createElement('div')).addClass('wrapper');
        const title = $(createElement('div')).addClass('title');
        const items = $(createElement('div')).addClass('items');

        title.html('Список обьектов');

        wrapper.appendChild(title);
        menu.appendChild(wrapper);
        menu.appendChild(items);


        const addItem = function (e) {
            const item = $(createElement('div'));
            item.html(`${e.type} #${e.id}`).addClass('item').addClass(e.type).attr('item-id');
            item.on('click', () => {
                item.toggleClass('active');
                if (item.issetClass('active')) {
                    dbg.add(e, ['id', 'type', 'x', 'y']);
                } else {
                    dbg.remove(e);
                }
            });
            items.appendChild(item);
        };


        let itemsIsActive = false;

        canvas.on('mouseenter', () => canvas.css('opacity', 1));
        canvas.on('mouseleave', () => canvas.css('opacity', .9));
        canvas.on('mousedown', e => {
            switch (e.which) {
                case 1: {
                    setType(statsType === 2 ? 0 : statsType + 1);
                    localStorage.setItem('point.debugger.type', statsType);
                    break;
                }
                case 3: {
                    menu.css({display: 'block'});
                    items.css({maxHeight: (pjs.game.getWH().h - 40) + 'px'});
                    itemsIsActive = true;
                    break;
                }
            }
        });
        canvas.on('contextmenu', e => {
            return e.preventDefault();
        });
        menu.on('contextmenu', e => {
            return e.preventDefault();
        });

        menu.on('mouseleave', () => {
            // menu.css('display', 'none')
        });

        document.onmousemove = function (e) {
            if (itemsIsActive && pjs.game.getWH().w - 305 > e.pageX) {
                menu.css('display', 'none');
                itemsIsActive = false;
            }
        };

        document.onreadystatechange = function () {
            if (document.readyState === "interactive") {
                $('body').appendChild(canvas).appendChild(menu);
            }
        };

        let statsType = 1;

        const statsSize = [[300, 105], [150, 32], [80, 32]];
        const maxWidth = statsSize[0][0];

        const setType = function (e) {
            statsType = e;

            canvas.selector[0].width = statsSize[statsType][0];
            canvas.selector[0].height = statsSize[statsType][1];
            canvas.css({
                width: statsSize[statsType][0],
                height: statsSize[statsType][1]
            });
        };

        const localStorageType = localStorage.getItem('point.debugger.type');
        setType(localStorageType !== null ? parseInt(localStorageType) : 0);


        const drawLine = function (option) {
            context.strokeStyle = option.color;
            context.lineWidth = option.width;
            context.beginPath();
            context.moveTo(option.x1, option.y1);
            context.lineTo(option.x2, option.y2);
            context.closePath();
            context.stroke();
        };

        const drawText = function (option) {
            context.font = `bold ${option.size}px Helvetica,Arial,sans-serif`;
            context.fillStyle = option.color;
            context.fillText(option.text, option.x, option.y);
        };

        const speed = {
            min: 0,
            max: 0,
            current: 0,
            list: [],
            color: '#0f0'
        };

        const fps = {
            min: 0,
            max: 60,
            current: 0,
            list: [],
            color: '#00FFFF'
        };

        for (let i = 0; i < maxWidth; i++) {
            speed.list[i] = 0;
            fps.list[i] = 0;
        }

        (function update() {

            context.clearRect(0, 0, statsSize[statsType][0], statsSize[statsType][1]);

            speed.list[maxWidth - 1] = speed.current;
            fps.list[maxWidth - 1] = fps.current;

            speed.min = Math.min.apply(null, speed.list);
            speed.max = Math.max.apply(null, speed.list);

            fps.min = Math.min.apply(null, fps.list);
            if (fps.current > fps.max) {
                fps.max = fps.current;
            }
            const maxFps = Math.max.apply(null, fps.list);

            if (statsType === 0 || statsType === 1) {
                for (let i = 0; i < maxWidth; i++) {
                    if (maxWidth === i + 1) {
                        continue;
                    }

                    if (statsType === 0) {
                        drawLine({
                            color: speed.color,
                            width: .5,
                            x1: i,
                            y1: Math.round((1 - (speed.list[i] / speed.max)) * statsHeight),
                            x2: i + 1,
                            y2: Math.round((1 - (speed.list[i + 1] / speed.max)) * statsHeight)
                        });

                        drawLine({
                            color: fps.color,
                            width: .5,
                            x1: i,
                            y1: Math.round((1 - (fps.list[i] / fps.max)) * statsHeight),
                            x2: i + 1,
                            y2: Math.round((1 - (fps.list[i + 1] / fps.max)) * statsHeight)
                        });
                    }

                    speed.list[i] = speed.list[i + 1];
                    fps.list[i] = fps.list[i + 1];
                }
            }

            if (statsType === 0) {
                drawLine({
                    color: '#000000',
                    width: .5,
                    x1: 0,
                    y1: statsHeight + 3,
                    x2: maxWidth,
                    y2: statsHeight + 3
                });
            }

            drawText({
                x: 2,
                y: statsType === 0 ? statsHeight + 19 : 12,
                size: 11,
                text: `FPS: ${fps.current}` + (statsType < 2 ? ` (${fps.min}-${maxFps})` : ''),
                color: fps.color
            });

            drawText({
                x: 2,
                y: statsType === 0 ? statsHeight + 37 : 27,
                size: 11,
                text: `SPEED: ${speed.current.toFixed(2)}` + (statsType < 2 ? ` (${speed.min.toFixed(2)}-${speed.max.toFixed(2)})` : ''),
                color: speed.color
            });

            (requestAnimationFrame || webkitRequestAnimationFrame || mozRequestAnimationFrame || oRequestAnimationFrame || msRequestAnimationFrame || function (cb) {
                setTimeout(cb, 1000 / 60)
            })(update);
        })();

        let beginTime = 0;
        let prevTime = beginTime;
        let frames = 0;

        const start = function () {
            beginTime = (performance || Date).now();
        };
        const end = function () {

            frames++;

            let time = ( performance || Date ).now();

            speed.current = time - beginTime;

            if (time >= prevTime + 1000) {

                fps.current = parseInt((frames * 1000) / (time - prevTime));

                prevTime = time;
                frames = 0;

            }

            beginTime = time;
        };

        let _isDebug = false;
        pjs.debug = function (e) {
            _isDebug = e;
            canvas.css('display', e ? 'block' : 'none');
        };


        pjs.OOP.forArr(['newBaseObject', 'newTriangleObject', 'newRectObject', 'newRoundRectObject', 'newCircleObject', 'newEllipsObject', 'newTextObject', 'newPolygonObject', 'newImageObject', 'newAnimationObject'], object => {
            pjs.game[object] = function (e) {
                return function () {
                    const apply = e.apply(this, arguments);
                    addItem(apply);
                    return apply;
                }
            }(pjs.game[object]);
        });

        pjs.game.newLoopFromClassObject = function (e) {
            return function () {
                if (typeof arguments[1].update === 'function') {
                    arguments[1].update = function (e) {
                        return function () {
                            _isDebug && start();
                            const apply = e.apply(this, arguments);
                            _isDebug && end();

                            _isDebug && dbg.draw();

                            return apply;
                        }
                    }(arguments[1].update);
                }
                return e.apply(this, arguments);
            }
        }(pjs.game.newLoopFromClassObject);
    }
}(PointJS);



